.page {
  font-family: "Poppins", sans-serif;
  color: $main-text-color;
  font-style: normal;
  font-size: 16px;
  font-weight: 400;
  line-height: 140%;

  scroll-behavior: smooth;

  &__body {
    margin: 0;
  }

  &__menu {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    width: 100vw;

    transition: all $effectDuration;
    opacity: 0;
    transform: translateX(-100%);
    pointer-events: none;

    &:target {
      opacity: 1;
      transform: translateX(0);
      pointer-events: all;

    }
  }
}

.page:has(.page__menu:target) {
  overflow: hidden;
}
